	import listCell from '@/components/mix-list-cell';
    import {  
        mapState 
    } from 'vuex';  
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell
		},
		data(){
			return {
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				userInfo:[],
				storeVisit:[],
				MyNaviga:[],
				orderNum:[],
			}
		},
		onLoad(){
			getApp().user_api(true);
			// this.my();
			// this.getStoreVisit();
			this.getMyNaviga();
		},
		onShow(){
			this.getOrderData();
			this.my();
			this.getStoreVisit();
		},
		
		
		methods: {
			my(){
				let self = this;
				let path = '/gzh/user_api/my'
				getApp().http(path, {}, function(res) {
					console.log('个人信息',res)
					self.userInfo = res;
				})
			},
			getStoreVisit(){
				let self = this;
				let path = '/gzh/store_api/get_store_visit'
				getApp().http(path, {}, function(res) {
					self.storeVisit = res;
				})
			},
			getMyNaviga(){
				let self = this;
				let path = '/gzh/public_api/get_my_naviga'
				getApp().http(path, {}, function(res) {
					console.log('列表',res)
					self.MyNaviga = res;
				})
			},
			
			getOrderData(){
				let self = this;
				let path = '/gzh/auth_api/get_order_data'
				getApp().http(path, {}, function(res) {
					self.orderNum = res;
				})
			},
			
			phone(){
				
				uni.showModal({
					title: '客服电话',
					content: '是否拨打客服电话',
					success: (md) => {
						console.log('sssss')
						if(md.confirm == true){
							uni.makePhoneCall({
							    phoneNumber: '111' //仅为示例
							});
						}
					}
				})
				
				
			},
			
			
			indexUrl(icon){
				let http = icon.indexOf('http')
				if(http == -1){
					return this.webUrl +'/'+icon
				}else{
					return icon;
				}
			},
			omney(item){
				uni.navigateTo({
					url:item
				}) 
			},
			navTo(item){
				uni.navigateTo({  
					url:'/pages/product/product?id='+item.id
				})  
			}, 
			navToOrder(item){
				uni.navigateTo({
					url:item
				}) 
			},
			coupon(){
				uni.navigateTo({
					url:'/pages/user/coupon'
				}) 
			},
			
		// 	/**
		// 	 *  会员卡下拉和回弹
		// 	 *  1.关闭bounce避免ios端下拉冲突
		// 	 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		// 	 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		// 	 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		// 	 */
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			// 会员下拉 
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
		
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			}
		}, 
		   
		
		
		
		
		onNavigationBarButtonTap(e) {
			const index = e.index;
			if (index === 0) {
				// this.navTo('/pages/set/set');
				uni.navigateTo({
					url: '/pages/notice/notice'
				})
			}else if(index === 1){
				
				// uni.navigateTo({
				// 	url: '/pages/notice/notice'
				// })
			}
		},
        computed: {
			// ...mapState(['hasLogin','userInfo'])
		},
       
	}  
